.TodoFilter {
    width: 400px;
  
    margin-bottom: 20px;
}

.TodoFilter__label {
    margin-top: 0;
    margin-bottom: 5px;
}

.TodoFilter__input {
    width: 100%;
    padding: 12px 16px;
    border: 1px solid rgba(33, 33, 33, 0.2);
    border-radius: 4px;

    font-family: inherit;
    line-height: 1.3;
    letter-spacing: 0.01em;
}